'use client';

import React, { useState, useEffect, useCallback, useRef } from 'react';
import { getApiUrl, getImageUrl } from '@/lib/api';
import Copyright from '@/components/Copyright';
import './SocialFeed.css';

interface Profile {
  cover_image: string;
  avatar: string;
  nickname: string;
  signature: string;
  custom_copyright: string;
}

interface Post {
  id: number;
  content: string;
  images: string[];
  thumbnails?: string[];  // Áº©Áï•ÂõæË∑ØÂæÑÊï∞ÁªÑ
  video: string | null;
  likes_count: number;
  comments_count: number;
  is_pinned: boolean;
  created_at: string;
}

// Âà§Êñ≠ÊòØÂê¶ÊòØËßÜÈ¢ëÊñá‰ª∂ - ÁßªÂà∞ÁªÑ‰ª∂Â§ñÈÉ®
// ÊîØÊåÅÊ†áÂáÜËßÜÈ¢ëÊâ©Â±ïÂêçÂíåÂ∏¶ #.mp4 Ê†áËÆ∞ÁöÑURLÔºàÁî®‰∫éÊäñÈü≥Á≠âÁ¨¨‰∏âÊñπËßÜÈ¢ëÔºâ
const isVideoFile = (url: string) => {
  const videoExtensions = ['.mp4', '.webm', '.ogg', '.mov', '.avi'];
  const lowerUrl = url.toLowerCase();
  // Ê£ÄÊü•Ê†áÂáÜÊâ©Â±ïÂêçÊàñ #.mp4 Ê†áËÆ∞
  return videoExtensions.some(ext => lowerUrl.endsWith(ext) || lowerUrl.endsWith('#' + ext.slice(1)));
};

// ÊáíÂä†ËΩΩÂõæÁâáÁªÑ‰ª∂ - ‰ΩøÁî® IntersectionObserver
const LazyImage = ({
  src,
  alt,
  onClick,
  className = ''
}: {
  src: string,
  alt: string,
  onClick?: () => void,
  className?: string
}) => {
  const imgRef = useRef<HTMLImageElement>(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isInView, setIsInView] = useState(false);
  const [hasError, setHasError] = useState(false);

  useEffect(() => {
    const img = imgRef.current;
    if (!img) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsInView(true);
            observer.disconnect();
          }
        });
      },
      { rootMargin: '100px', threshold: 0.1 }
    );

    observer.observe(img);
    return () => observer.disconnect();
  }, []);

  // Â§ÑÁêÜÂõæÁâáÂä†ËΩΩÈîôËØØ - ÂØπ‰∫éÁ¨¨‰∏âÊñπÂõæÁâáÔºåÁõ¥Êé•‰ΩøÁî®ÂéüÂõæ
  const handleError = () => {
    setHasError(true);
    setIsLoaded(true);
  };

  return (
    <img
      ref={imgRef}
      src={isInView ? src : undefined}
      data-src={src}
      alt={alt}
      onClick={onClick}
      className={`${className} ${isLoaded ? 'sf-img-loaded' : 'sf-img-loading'}`}
      onLoad={() => setIsLoaded(true)}
      onError={handleError}
      style={{
        backgroundColor: isLoaded ? 'transparent' : undefined,
        display: hasError && !isLoaded ? 'none' : undefined
      }}
    />
  );
};

// Ê∏ÖÁêÜËßÜÈ¢ëURL - ÁßªÈô§ #.mp4 Á≠âÊ†áËÆ∞
const cleanVideoUrl = (url: string) => {
  // ÁßªÈô§ #.mp4, #.webm Á≠âÊ†áËÆ∞
  return url.replace(/#\.(mp4|webm|ogg|mov|avi)$/i, '');
};

// Âà§Êñ≠ÊòØÂê¶ÊòØÂ§ñÈÉ®URLÔºàÁ¨¨‰∏âÊñπÂ≠òÂÇ®Ôºâ
const isExternalUrl = (url: string) => {
  return url.startsWith('http://') || url.startsWith('https://');
};

// ÊáíÂä†ËΩΩËßÜÈ¢ëÁªÑ‰ª∂ - Âè™È¢ÑÂä†ËΩΩ metadataÔºåÁÇπÂáªÊâçÂä†ËΩΩÂÆåÊï¥ËßÜÈ¢ëÔºåÁ¶ªÂºÄËßÜÂè£Ëá™Âä®ÊöÇÂÅú
const LazyVideo = ({ video, images }: { video: string | null, images: string[] }) => {
  const rawVideoUrl = video || images.find(img => isVideoFile(img));
  const videoRef = useRef<HTMLVideoElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const [isInView, setIsInView] = useState(false);
  const [isActivated, setIsActivated] = useState(false);
  const [showPlayButton, setShowPlayButton] = useState(true);

  // ÊáíÂä†ËΩΩ - ËøõÂÖ•ËßÜÂè£Êó∂Âä†ËΩΩËßÜÈ¢ë
  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsInView(true);
            observer.disconnect();
          }
        });
      },
      { rootMargin: '50px', threshold: 0.1 }
    );

    observer.observe(container);
    return () => observer.disconnect();
  }, []);

  // Á¶ªÂºÄËßÜÂè£Êó∂ÊöÇÂÅúËßÜÈ¢ë
  useEffect(() => {
    const container = containerRef.current;
    const videoEl = videoRef.current;
    if (!container || !videoEl) return;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (!entry.isIntersecting && !videoEl.paused) {
            videoEl.pause();
          }
        });
      },
      { threshold: 0 }
    );

    observer.observe(container);
    return () => observer.disconnect();
  }, [isInView]); // Âè™Âú®ËßÜÈ¢ëÂä†ËΩΩÂêéÊâçÂêØÂä®Ëøô‰∏™ observer

  if (!rawVideoUrl) return null;

  // Ê∏ÖÁêÜURLÂπ∂Âà§Êñ≠ÊòØÂê¶ÊòØÂ§ñÈÉ®ÈìæÊé•
  const cleanedUrl = cleanVideoUrl(rawVideoUrl);
  const isExternal = isExternalUrl(cleanedUrl);
  // Â§ñÈÉ®ËßÜÈ¢ëÈÄöËøáÂêéÁ´Ø‰ª£ÁêÜÊí≠ÊîæÔºàËß£ÂÜ≥Ë∑®ÂüüÂíåÈò≤ÁõóÈìæÈóÆÈ¢òÔºâ
  const finalVideoUrl = isExternal
    ? getApiUrl(`/proxy/video?url=${encodeURIComponent(cleanedUrl)}`)
    : getImageUrl(cleanedUrl);

  const handlePlay = () => {
    setIsActivated(true);
    setShowPlayButton(false);
    setTimeout(() => {
      videoRef.current?.play();
    }, 100);
  };

  const handleVideoPlay = () => {
    setShowPlayButton(false);
  };

  const handleVideoPause = () => {
    setShowPlayButton(true);
  };

  return (
    <div className="sf-video" ref={containerRef}>
      {isInView && (
        <div className="sf-video-wrapper">
          <video
            ref={videoRef}
            src={finalVideoUrl}
            controls
            preload={isActivated ? 'auto' : 'metadata'}
            onPlay={handleVideoPlay}
            onPause={handleVideoPause}
            playsInline
          />
          {showPlayButton && (
            <div className="sf-video-play-btn" onClick={handlePlay}>
              <svg viewBox="0 0 24 24" fill="currentColor">
                <path d="M8 5v14l11-7z" />
              </svg>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

// ÂõæÁâáÁΩëÊ†ºÁªÑ‰ª∂ - ‰ΩøÁî®ÊáíÂä†ËΩΩÂíåÁº©Áï•Âõæ
const ImageGrid = ({
  images,
  thumbnails,
  isFourImages = false,
  onImageClick
}: {
  images: string[],
  thumbnails?: string[],
  isFourImages?: boolean,
  onImageClick: (images: string[], index: number) => void
}) => {
  const imageFiles = images.filter(img => !isVideoFile(img));
  if (imageFiles.length === 0) return null;

  // Ëé∑ÂèñÂØπÂ∫îÁöÑÁº©Áï•ÂõæÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ΩøÁî®ÂéüÂõæ
  // ÂØπ‰∫éÁ¨¨‰∏âÊñπÂ≠òÂÇ®ÁöÑÂõæÁâáÔºàÂ§ñÈÉ®URLÔºâÔºåÁõ¥Êé•‰ΩøÁî®ÂéüÂõæÔºå‰∏ç‰ΩøÁî®Áº©Áï•Âõæ
  const getThumbnail = (index: number) => {
    const originalUrl = imageFiles[index];
    // Â¶ÇÊûúÊòØÂ§ñÈÉ®URLÔºåÁõ¥Êé•ËøîÂõûÂéüÂõæ
    if (isExternalUrl(originalUrl)) {
      return originalUrl;
    }
    // Êú¨Âú∞ÂõæÁâá‰ΩøÁî®Áº©Áï•Âõæ
    if (thumbnails && thumbnails[index]) {
      return getImageUrl(thumbnails[index]);
    }
    return getImageUrl(originalUrl);
  };

  if (imageFiles.length === 1) {
    return (
      <div className="sf-pic-grid sf-pic-1">
        <LazyImage
          src={getThumbnail(0)}
          onClick={() => onImageClick(imageFiles, 0)}
          alt="Âä®ÊÄÅÂõæÁâá"
        />
      </div>
    );
  }

  return (
    <div className={`sf-pic-grid ${isFourImages ? 'sf-pic-4' : ''}`}>
      {imageFiles.map((img, index) => (
        <LazyImage
          key={index}
          src={getThumbnail(index)}
          onClick={() => onImageClick(imageFiles, index)}
          alt={`Âä®ÊÄÅÂõæÁâá${index + 1}`}
        />
      ))}
    </div>
  );
};

// ÂèØÂ±ïÂºÄ/Êî∂Ëµ∑ÁöÑÊñáÊú¨ÁªÑ‰ª∂ - ‰ΩøÁî® memo Èò≤Ê≠¢‰∏çÂøÖË¶ÅÁöÑÈáçÊñ∞Ê∏≤Êüì
const ExpandableText = React.memo(({ content, maxLines = 4 }: { content: string, maxLines?: number }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [needsExpand, setNeedsExpand] = useState(false);
  const textRef = useRef<HTMLDivElement>(null);
  const fullTextRef = useRef<HTMLDivElement>(null);
  const contentIdRef = useRef(0);

  // Ê£ÄÊü•ÂÜÖÂÆπÊòØÂê¶ÂåÖÂê´ iframe
  const hasIframe = content && content.includes('<iframe');

  // ÂàÜÁ¶ªÊñáÂ≠óÂÜÖÂÆπÂíå iframe ÂÜÖÂÆπ
  const separateContent = (text: string) => {
    if (!text) return { textPart: '', iframePart: '' };

    // ÂåπÈÖç iframe Ê†áÁ≠æÔºàÂåÖÊã¨ÂÜÖÂÆπÂíåÁªìÊùüÊ†áÁ≠æÔºâ
    const iframeRegex = /<iframe[\s\S]*?<\/iframe>/gi;
    const iframes = text.match(iframeRegex) || [];
    const textPart = text.replace(iframeRegex, '').trim();
    const iframePart = iframes.join('');

    return { textPart, iframePart };
  };

  const { textPart, iframePart } = separateContent(content);

  // Â§ÑÁêÜÂÜÖÂÆπÔºöÊç¢Ë°åÁ¨¶ËΩ¨ <br>ÔºåÂ§ÑÁêÜ iframe
  const processContent = (text: string) => {
    if (!text) return '';
    // ÂÖàÂ∞ÜÊç¢Ë°åÁ¨¶ËΩ¨Êç¢‰∏∫ <br>
    let processed = text.replace(/\n/g, '<br>');
    // Â§ÑÁêÜ iframe Á¶ÅÊ≠¢Ëá™Âä®Êí≠ÊîæÔºåÂπ∂Ê∑ªÂä† loading="lazy"
    // ÁΩëÊòì‰∫ëÈü≥‰πê iframe Ê∑ªÂä†ÂåÖË£πÂÆπÂô®ÔºàÁî®‰∫éÊ∑±Ëâ≤Ê®°ÂºèË£ÅÂâ™Èò¥ÂΩ±Ôºâ
    processed = processed.replace(/<iframe([^>]*)src="([^"]*)"([^>]*)>([\s\S]*?)<\/iframe>/gi, (match, before, src, after, content) => {
      let newSrc = src;
      if (!src.includes('autoplay=')) {
        newSrc = src + (src.includes('?') ? '&' : '?') + 'autoplay=0';
      } else {
        newSrc = src.replace(/autoplay=1/gi, 'autoplay=0');
      }
      // Ê∑ªÂä† loading="lazy" Èò≤Ê≠¢‰∏çÂøÖË¶ÅÁöÑÈáçÊñ∞Âä†ËΩΩ
      const hasLoading = before.includes('loading=') || after.includes('loading=');
      const loadingAttr = hasLoading ? '' : ' loading="lazy"';
      const iframeHtml = `<iframe${before}src="${newSrc}"${after}${loadingAttr}>${content}</iframe>`;

      // ÁΩëÊòì‰∫ëÈü≥‰πê iframe Áî®ÂÆπÂô®ÂåÖË£πÔºàÊ∑±Ëâ≤Ê®°Âºè‰∏ãÁî®‰∫éË£ÅÂâ™Èò¥ÂΩ±Ôºâ
      if (src.includes('music.163.com')) {
        return `<div class="sf-music163-wrapper">${iframeHtml}</div>`;
      }
      return iframeHtml;
    });
    return processed;
  };

  const processedTextHtml = processContent(textPart);
  const processedIframeHtml = processContent(iframePart);

  // Âè™Âú® content ÂèòÂåñÊó∂Êõ¥Êñ∞ key
  useEffect(() => {
    contentIdRef.current += 1;
  }, [content]);

  // Ê£ÄÊµãÊñáÂ≠óÈÉ®ÂàÜÊòØÂê¶ÈúÄË¶ÅÂ±ïÂºÄÊåâÈíÆ
  useEffect(() => {
    const checkHeight = () => {
      const textEl = textRef.current;
      const fullEl = fullTextRef.current;
      if (!textEl || !fullEl) return;

      // Âè™Ê£ÄÊµãÊñáÂ≠óÈÉ®ÂàÜÁöÑÈ´òÂ∫¶
      const lineHeight = parseFloat(getComputedStyle(textEl).lineHeight) || 21;
      const maxHeight = lineHeight * maxLines;

      const actualHeight = fullEl.scrollHeight;
      if (actualHeight > maxHeight + 5) {
        setNeedsExpand(true);
      } else {
        setNeedsExpand(false);
      }
    };

    // Âª∂ËøüÊ£ÄÊµãÔºåÁ°Æ‰øù DOM Ê∏≤ÊüìÂÆåÊàê
    const timer = setTimeout(checkHeight, 100);
    return () => {
      clearTimeout(timer);
    };
  }, [textPart, maxLines]);

  // ‰ΩøÁî® key Êù•ÊéßÂà∂ iframe ÁöÑÈáçÊñ∞Ê∏≤ÊüìÔºåÂè™Êúâ content ÂèòÂåñÊó∂ÊâçÈáçÊñ∞Ê∏≤Êüì
  const contentKey = `content-${contentIdRef.current}`;

  // Â¶ÇÊûúÊúâ iframeÔºåÂàÜÂºÄÊ∏≤ÊüìÊñáÂ≠óÂíå iframe
  if (hasIframe) {
    return (
      <div className="sf-text-wrapper">
        {/* ÊñáÂ≠óÈÉ®ÂàÜ */}
        {textPart && (
          <>
            {/* ÈöêËóèÁöÑÂÆåÊï¥ÊñáÂ≠óÂÜÖÂÆπÔºåÁî®‰∫éÊµãÈáèÈ´òÂ∫¶ */}
            <div
              ref={fullTextRef}
              className="sf-text sf-text-measure"
              dangerouslySetInnerHTML={{ __html: processedTextHtml }}
              style={{ position: 'absolute', visibility: 'hidden', width: '100%', pointerEvents: 'none' }}
            />

            {/* ÊòæÁ§∫ÁöÑÊñáÂ≠óÂÜÖÂÆπ */}
            <div
              ref={textRef}
              className={`sf-text ${!isExpanded && needsExpand ? 'sf-text-collapsed' : ''}`}
              style={!isExpanded && needsExpand ? {
                WebkitLineClamp: maxLines,
                display: '-webkit-box',
                WebkitBoxOrient: 'vertical',
                overflow: 'hidden'
              } as React.CSSProperties : undefined}
              dangerouslySetInnerHTML={{ __html: processedTextHtml }}
            />

            {/* Â±ïÂºÄ/Êî∂Ëµ∑ÊåâÈíÆ - ÊîæÂú® iframe ‰∏äÊñπ */}
            {needsExpand && (
              <span
                className="sf-text-toggle"
                onClick={() => setIsExpanded(!isExpanded)}
              >
                {isExpanded ? 'Êî∂Ëµ∑' : 'Â±ïÂºÄ'}
              </span>
            )}
          </>
        )}

        {/* iframe ÈÉ®ÂàÜ - ÂßãÁªàÂÆåÊï¥ÊòæÁ§∫ */}
        <div
          className="sf-text sf-iframe-container"
          dangerouslySetInnerHTML={{ __html: processedIframeHtml }}
        />
      </div>
    );
  }

  // Ê≤°Êúâ iframe ÁöÑÊÉÖÂÜµÔºå‰øùÊåÅÂéüÊúâÈÄªËæë
  const processedHtml = processContent(content);

  return (
    <div className="sf-text-wrapper">
      {/* ÈöêËóèÁöÑÂÆåÊï¥ÂÜÖÂÆπÔºåÁî®‰∫éÊµãÈáèÈ´òÂ∫¶ */}
      <div
        ref={fullTextRef}
        className="sf-text sf-text-measure"
        dangerouslySetInnerHTML={{ __html: processedHtml }}
        style={{ position: 'absolute', visibility: 'hidden', width: '100%', pointerEvents: 'none' }}
      />

      {/* ÊòæÁ§∫ÁöÑÂÜÖÂÆπ */}
      <div
        key={contentKey}
        ref={textRef}
        className={`sf-text ${!isExpanded && needsExpand ? 'sf-text-collapsed' : ''}`}
        style={!isExpanded && needsExpand ? {
          WebkitLineClamp: maxLines,
          display: '-webkit-box',
          WebkitBoxOrient: 'vertical',
          overflow: 'hidden'
        } as React.CSSProperties : undefined}
        dangerouslySetInnerHTML={{ __html: processedHtml }}
      />

      {/* Â±ïÂºÄ/Êî∂Ëµ∑ÊåâÈíÆ */}
      {needsExpand && (
        <span
          className="sf-text-toggle"
          onClick={() => setIsExpanded(!isExpanded)}
        >
          {isExpanded ? 'Êî∂Ëµ∑' : 'Â±ïÂºÄ'}
        </span>
      )}
    </div>
  );
});

export default function SocialFeedPage() {
  const [profile, setProfile] = useState<Profile>({
    cover_image: '',
    avatar: '',
    nickname: 'ÊúãÂèãÂúà',
    signature: '',
    custom_copyright: ''
  });
  const [posts, setPosts] = useState<Post[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // ÂàÜÈ°µÁä∂ÊÄÅ
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const loadMoreRef = useRef<HTMLDivElement>(null);
  const POSTS_PER_PAGE = 10;

  // Lightbox Áä∂ÊÄÅ
  const [currentImageList, setCurrentImageList] = useState<string[]>([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [showLightbox, setShowLightbox] = useState(false);

  // Ëß¶Êë∏ÊªëÂä®Áä∂ÊÄÅ
  const [touchStart, setTouchStart] = useState<number | null>(null);
  const [touchEnd, setTouchEnd] = useState<number | null>(null);

  // ÂõæÁâáÁº©ÊîæÂíåÊãñÂä®Áä∂ÊÄÅ
  const [scale, setScale] = useState(1);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState({ x: 0, y: 0 });
  const [lastPinchDistance, setLastPinchDistance] = useState<number | null>(null);
  const imageContainerRef = useRef<HTMLDivElement>(null);

  // ÂõûÂà∞È°∂ÈÉ®ÊÇ¨ÊµÆÁêÉÁä∂ÊÄÅ
  const [showBackToTop, setShowBackToTop] = useState(false);

  // Lightbox ÂõæÁâáÂä†ËΩΩÁä∂ÊÄÅ
  const [imageLoadingStates, setImageLoadingStates] = useState<Record<number, boolean>>({});

  // È°µÈù¢Áã¨Á´ã‰∏ªÈ¢òÁä∂ÊÄÅ - ÈªòËÆ§ÊµÖËâ≤
  const [isDark, setIsDark] = useState(false);

  // ÊúãÂèãÂúà‰∏ªÈ¢òÊ®°ÂºèÁä∂ÊÄÅ
  const [socialFeedThemeEnabled, setSocialFeedThemeEnabled] = useState(false);

  // È°µÈù¢ÊñáÊú¨ÈÖçÁΩÆÁä∂ÊÄÅ - ÂàùÂßãÂÄº‰∏∫Á©∫ÔºåÈÅøÂÖçË¶ÜÁõñ layout ‰∏≠ËÆæÁΩÆÁöÑÊ†áÈ¢ò
  const [pageTextConfig, setPageTextConfig] = useState({
    browserTitle: '',
    browserSubtitle: ''
  });

  // ËÆæÁΩÆÊµèËßàÂô®Ê†áÈ¢òÔºà‰ªÖ‰ΩøÁî® browserTitleÔºå‰∏çÂèóÁΩëÁ´ôÊ†áÈ¢ò„ÄÅÂâØÊ†áÈ¢òÂΩ±ÂìçÔºâ
  useEffect(() => {
    if (!pageTextConfig.browserTitle) return;
    let title = pageTextConfig.browserTitle;
    if (pageTextConfig.browserSubtitle) {
      title = `${title} - ${pageTextConfig.browserSubtitle}`;
    }
    document.title = title;
  }, [pageTextConfig.browserTitle, pageTextConfig.browserSubtitle]);

  // Ëé∑ÂèñÁ≥ªÁªüËÆæÁΩÆÂíåÈ°µÈù¢ÊñáÊú¨ÈÖçÁΩÆ
  useEffect(() => {
    const fetchSettings = async () => {
      try {
        // Âπ∂Ë°åËé∑ÂèñËÆæÁΩÆÂíåÈ°µÈù¢ÊñáÊú¨ÈÖçÁΩÆ
        const [settingsRes, pageTextsRes] = await Promise.all([
          fetch(getApiUrl('/settings')),
          fetch(getApiUrl('/page-texts/socialFeed'))
        ]);

        if (settingsRes.ok) {
          const data = await settingsRes.json();
          if (data.success && data.data) {
            const settingsObj: Record<string, string> = {};
            data.data.forEach((item: { key: string; value: string }) => {
              settingsObj[item.key] = item.value;
            });
            setSocialFeedThemeEnabled(settingsObj.socialFeedThemeEnabled === 'true');
          }
        }

        if (pageTextsRes.ok) {
          const data = await pageTextsRes.json();
          if (data.success && data.data) {
            setPageTextConfig({
              browserTitle: data.data.browserTitle || 'ÊúãÂèãÂúà',
              browserSubtitle: data.data.browserSubtitle || ''
            });
          }
        }
      } catch (err) {
        console.error('Ëé∑ÂèñËÆæÁΩÆÂ§±Ë¥•:', err);
      }
    };
    fetchSettings();
  }, []);

  // Ëß¶Êë∏ÊªëÂä®Â§ÑÁêÜ - ÊúÄÂ∞èÊªëÂä®Ë∑ùÁ¶ª
  const minSwipeDistance = 50;

  // ËÆ°ÁÆó‰∏§Êåá‰πãÈó¥ÁöÑË∑ùÁ¶ª
  const getPinchDistance = (touches: React.TouchList) => {
    return Math.hypot(
      touches[0].clientX - touches[1].clientX,
      touches[0].clientY - touches[1].clientY
    );
  };

  const onTouchStart = (e: React.TouchEvent) => {
    if (e.touches.length === 2) {
      // ÂèåÊåáËß¶Êë∏ - ÂºÄÂßãÁº©Êîæ
      e.preventDefault();
      setLastPinchDistance(getPinchDistance(e.touches));
    } else if (e.touches.length === 1) {
      if (scale > 1) {
        // Â∑≤Áº©ÊîæÁä∂ÊÄÅ - ÂºÄÂßãÊãñÂä®
        setIsDragging(true);
        setDragStart({
          x: e.touches[0].clientX - position.x,
          y: e.touches[0].clientY - position.y
        });
      } else {
        // Êú™Áº©ÊîæÁä∂ÊÄÅ - ÊªëÂä®ÂàáÊç¢ÂõæÁâá
        setTouchEnd(null);
        setTouchStart(e.touches[0].clientX);
      }
    }
  };

  const onTouchMove = (e: React.TouchEvent) => {
    if (e.touches.length === 2 && lastPinchDistance !== null) {
      // ÂèåÊåáÁº©Êîæ
      e.preventDefault();
      const currentDistance = getPinchDistance(e.touches);
      const delta = currentDistance / lastPinchDistance;
      setLastPinchDistance(currentDistance);
      setScale(prev => Math.min(Math.max(prev * delta, 1), 5));
    } else if (e.touches.length === 1) {
      if (isDragging && scale > 1) {
        // ÊãñÂä®ÂõæÁâá
        e.preventDefault();
        setPosition({
          x: e.touches[0].clientX - dragStart.x,
          y: e.touches[0].clientY - dragStart.y
        });
      } else if (scale === 1) {
        // ÊªëÂä®ÂàáÊç¢
        setTouchEnd(e.touches[0].clientX);
      }
    }
  };

  const onTouchEnd = () => {
    setLastPinchDistance(null);
    setIsDragging(false);

    // Âè™ÊúâÂú®Êú™Áº©ÊîæÁä∂ÊÄÅ‰∏ãÊâçÂ§ÑÁêÜÊªëÂä®ÂàáÊç¢
    if (scale === 1 && touchStart !== null && touchEnd !== null) {
      const distance = touchStart - touchEnd;
      const isLeftSwipe = distance > minSwipeDistance;
      const isRightSwipe = distance < -minSwipeDistance;

      if (isLeftSwipe && currentIndex < currentImageList.length - 1) {
        setCurrentIndex(prev => prev + 1);
      }
      if (isRightSwipe && currentIndex > 0) {
        setCurrentIndex(prev => prev - 1);
      }
    }

    setTouchStart(null);
    setTouchEnd(null);
  };

  // Èº†Ê†áÊªöËΩÆÁº©ÊîæÔºàÁîµËÑëÁ´ØÔºâ
  const onWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? 0.9 : 1.1;
    setScale(prev => {
      const newScale = Math.min(Math.max(prev * delta, 1), 5);
      // Â¶ÇÊûúÁº©ÊîæÂõû1ÔºåÈáçÁΩÆ‰ΩçÁΩÆ
      if (newScale === 1) {
        setPosition({ x: 0, y: 0 });
      }
      return newScale;
    });
  };

  // Èº†Ê†áÊãñÂä®ÔºàÁîµËÑëÁ´ØÔºâ
  const onMouseDown = (e: React.MouseEvent) => {
    if (scale > 1) {
      e.preventDefault();
      setIsDragging(true);
      setDragStart({
        x: e.clientX - position.x,
        y: e.clientY - position.y
      });
    }
  };

  const onMouseMove = (e: React.MouseEvent) => {
    if (isDragging && scale > 1) {
      setPosition({
        x: e.clientX - dragStart.x,
        y: e.clientY - dragStart.y
      });
    }
  };

  const onMouseUp = () => {
    setIsDragging(false);
  };

  // ÂèåÂáªÈáçÁΩÆÁº©Êîæ
  const onDoubleClick = () => {
    if (scale > 1) {
      setScale(1);
      setPosition({ x: 0, y: 0 });
    } else {
      setScale(2);
    }
  };

  // ÂàáÊç¢ÂõæÁâáÊó∂ÈáçÁΩÆÁº©ÊîæÁä∂ÊÄÅ
  useEffect(() => {
    setScale(1);
    setPosition({ x: 0, y: 0 });
  }, [currentIndex]);

  // Âä†ËΩΩÊõ¥Â§öÂ∏ñÂ≠ê
  const loadMorePosts = useCallback(async () => {
    if (loadingMore || !hasMore) return;

    setLoadingMore(true);
    try {
      const res = await fetch(getApiUrl(`/social-feed/posts?page=${page + 1}&limit=${POSTS_PER_PAGE}`));
      if (res.ok) {
        const data = await res.json();
        if (data.success && data.data?.posts) {
          const newPosts = data.data.posts;
          if (newPosts.length < POSTS_PER_PAGE) {
            setHasMore(false);
          }
          if (newPosts.length > 0) {
            setPosts(prev => [...prev, ...newPosts]);
            setPage(prev => prev + 1);
          } else {
            setHasMore(false);
          }
        }
      }
    } catch (err) {
      console.error('Âä†ËΩΩÊõ¥Â§öÂ§±Ë¥•:', err);
    } finally {
      setLoadingMore(false);
    }
  }, [page, loadingMore, hasMore]);

  // Êó†ÈôêÊªöÂä® - IntersectionObserver
  useEffect(() => {
    const loadMoreElement = loadMoreRef.current;
    if (!loadMoreElement) return;

    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting && hasMore && !loadingMore && !loading) {
          loadMorePosts();
        }
      },
      { rootMargin: '100px', threshold: 0.1 }
    );

    observer.observe(loadMoreElement);
    return () => observer.disconnect();
  }, [hasMore, loadingMore, loading, loadMorePosts]);

  // ÂàùÂßãÂä†ËΩΩÊï∞ÊçÆ
  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        setError(null);

        const [profileRes, postsRes] = await Promise.all([
          fetch(getApiUrl('/social-feed/profile')),
          fetch(getApiUrl(`/social-feed/posts?page=1&limit=${POSTS_PER_PAGE}`))
        ]);

        if (profileRes.ok) {
          const profileData = await profileRes.json();
          if (profileData.success && profileData.data) {
            setProfile(profileData.data);
          }
        }

        if (postsRes.ok) {
          const postsData = await postsRes.json();
          if (postsData.success && postsData.data?.posts) {
            const fetchedPosts = postsData.data.posts;
            setPosts(fetchedPosts);
            // Âà§Êñ≠ÊòØÂê¶ËøòÊúâÊõ¥Â§ö
            if (fetchedPosts.length < POSTS_PER_PAGE) {
              setHasMore(false);
            }
          }
        }
      } catch (err) {
        console.error('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•:', err);
        setError('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, []);

  // ÁõëÂê¨ÊªöÂä®
  useEffect(() => {
    const handleScroll = () => {
      setShowBackToTop(window.scrollY > 300);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // ÈîÆÁõò‰∫ã‰ª∂ÁõëÂê¨ - Áî®‰∫é Lightbox Â∑¶Âè≥ÂàáÊç¢ÂíåÂÖ≥Èó≠
  useEffect(() => {
    if (!showLightbox) return;

    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'ArrowLeft' && currentIndex > 0) {
        setCurrentIndex(prev => prev - 1);
      } else if (e.key === 'ArrowRight' && currentIndex < currentImageList.length - 1) {
        setCurrentIndex(prev => prev + 1);
      } else if (e.key === 'Escape') {
        closeLightbox();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [showLightbox, currentIndex, currentImageList.length]);

  // ÂõûÂà∞È°∂ÈÉ®
  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  // ÂàáÊç¢‰∏ªÈ¢ò
  const toggleTheme = () => {
    setIsDark(prev => !prev);
  };

  // Ê†ºÂºèÂåñÊó∂Èó¥
  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diff = now.getTime() - date.getTime();
    const minutes = Math.floor(diff / (1000 * 60));
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));

    if (minutes < 1) return 'ÂàöÂàö';
    if (minutes < 60) return `${minutes}ÂàÜÈíüÂâç`;
    if (hours < 24) return `${hours}Â∞èÊó∂Ââç`;
    if (days < 7) return `${days}Â§©Ââç`;

    return date.toLocaleDateString('zh-CN', { month: 'long', day: 'numeric' });
  };

  // ÊâìÂºÄÂõæÁâáÈ¢ÑËßà
  const openGallery = useCallback((images: string[], index: number) => {
    // Â§ÑÁêÜÂõæÁâáURLÔºåÂ§ñÈÉ®URLÁõ¥Êé•‰ΩøÁî®ÔºåÊú¨Âú∞ÂõæÁâá‰ΩøÁî®getImageUrl
    const processedImages = images.map(img => {
      if (isExternalUrl(img)) {
        return img;
      }
      return getImageUrl(img);
    });
    setCurrentImageList(processedImages);
    setCurrentIndex(index);
    setImageLoadingStates({}); // ÈáçÁΩÆÂä†ËΩΩÁä∂ÊÄÅ
    setShowLightbox(true);
    document.body.style.overflow = 'hidden';
  }, []);

  // ÂÖ≥Èó≠ÂõæÁâáÈ¢ÑËßà
  const closeLightbox = () => {
    setShowLightbox(false);
    document.body.style.overflow = '';
    setCurrentIndex(0);
    setCurrentImageList([]);
  };

  // ÂàáÊç¢ÂõæÁâá
  const changeImage = (direction: number) => {
    const newIndex = currentIndex + direction;
    if (newIndex >= 0 && newIndex < currentImageList.length) {
      setCurrentIndex(newIndex);
    }
  };

  // Âä†ËΩΩÁä∂ÊÄÅ
  if (loading) {
    return (
      <div className={`sf-page ${isDark ? 'sf-dark' : 'sf-light'}`}>
        <div className="sf-loading">Âä†ËΩΩ‰∏≠...</div>
      </div>
    );
  }

  // ÈîôËØØÁä∂ÊÄÅ
  if (error) {
    return (
      <div className={`sf-page ${isDark ? 'sf-dark' : 'sf-light'}`}>
        <div className="sf-error">
          <div style={{ fontSize: '48px', marginBottom: '20px' }}>üòî</div>
          <div>{error}</div>
        </div>
      </div>
    );
  }

  return (
    <div className={`sf-page ${isDark ? 'sf-dark' : 'sf-light'}`}>
      <div className="sf-container">
        {/* È°∂ÈÉ®Â∞ÅÈù¢ */}
        <div className="sf-header">
          {/* ËøîÂõûÈ¶ñÈ°µÊåâÈíÆ - ÊúãÂèãÂúà‰∏ªÈ¢òÊ®°ÂºèÂºÄÂêØÊó∂ÈöêËóè */}
          {!socialFeedThemeEnabled && (
            <a
              href="/"
              className="sf-back-home"
              title="ËøîÂõûÈ¶ñÈ°µ"
            >
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round">
                <path d="M19 12H5M12 19l-7-7 7-7"/>
              </svg>
            </a>
          )}
          <img
            className="sf-cover"
            src={profile.cover_image ? getImageUrl(profile.cover_image) : '/pyqbj.png'}
            alt="Â∞ÅÈù¢"
          />
          <div className="sf-nickname">{profile.nickname || 'Arran'}</div>
          <div className="sf-signature">{profile.signature || 'ÂàÜ‰∫´ÁîüÊ¥ªÔºåËÆ∞ÂΩïÁæéÂ•ΩÊó∂Âàª'}</div>
          <div className="sf-avatar">
            <img
              src={profile.avatar ? getImageUrl(profile.avatar) : '/pyqtx.png'}
              alt="Â§¥ÂÉè"
            />
          </div>
        </div>

        {/* Âä®ÊÄÅÂàóË°® */}
        <div className="sf-feed-list">
          {posts.length === 0 ? (
            <div className="sf-empty">ÊöÇÊó†Âä®ÊÄÅ</div>
          ) : (
            posts.map((post) => (
              <div key={post.id} className="sf-feed-item">
                <div className="sf-avatar-col">
                  <img
                    src={profile.avatar ? getImageUrl(profile.avatar) : '/pyqtx.png'}
                    loading="lazy"
                    alt={profile.nickname}
                  />
                </div>
                <div className="sf-content-col">
                  <div className="sf-user-name">{profile.nickname || 'ÊúãÂèãÂúà'}</div>
                  <ExpandableText content={post.content} maxLines={4} />

                  {/* ËßÜÈ¢ë - ÂÖºÂÆπ video Â≠óÊÆµÂíå images ‰∏≠ÁöÑËßÜÈ¢ëÔºåÊáíÂä†ËΩΩ */}
                  <LazyVideo video={post.video} images={post.images || []} />

                  {/* ÂõæÁâáÁΩëÊ†º - Ëá™Âä®ËøáÊª§ËßÜÈ¢ëÊñá‰ª∂Ôºå‰ΩøÁî®Áº©Áï•Âõæ */}
                  {post.images && post.images.length > 0 && (
                    <ImageGrid
                      images={post.images}
                      thumbnails={post.thumbnails}
                      isFourImages={post.images.filter(img => !isVideoFile(img)).length === 4}
                      onImageClick={openGallery}
                    />
                  )}

                  <div className="sf-footer">
                    <span className="sf-time">{formatTime(post.created_at)}</span>
                    {post.is_pinned && <span className="sf-pin">ÁΩÆÈ°∂</span>}
                  </div>
                </div>
              </div>
            ))
          )}

          {/* Êó†ÈôêÊªöÂä®Ëß¶ÂèëÂô® */}
          <div ref={loadMoreRef} className="sf-load-more">
            {loadingMore && <div className="sf-loading-more">Âä†ËΩΩ‰∏≠...</div>}
            {!hasMore && posts.length > 0 && <div className="sf-no-more">Â∑≤Âä†ËΩΩÂÖ®ÈÉ®Âä®ÊÄÅ</div>}
          </div>
        </div>

        {/* ÁâàÊùÉ‰ø°ÊÅØ */}
        <div className="sf-copyright">
          {profile.custom_copyright && (
            <div className="sf-custom-copyright">{profile.custom_copyright}</div>
          )}
          <Copyright />
        </div>

        {/* Lightbox */}
        {showLightbox && (
          <div
            className="sf-lightbox"
            onTouchStart={onTouchStart}
            onTouchMove={onTouchMove}
            onTouchEnd={onTouchEnd}
            onMouseMove={onMouseMove}
            onMouseUp={onMouseUp}
            onMouseLeave={onMouseUp}
          >
            <div className="sf-lb-close" onClick={closeLightbox}>
              <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                <line x1="18" y1="6" x2="6" y2="18"></line>
                <line x1="6" y1="6" x2="18" y2="18"></line>
              </svg>
            </div>
            {currentImageList.length > 1 && (
              <>
                <div
                  className={`sf-lb-prev ${currentIndex === 0 ? 'sf-lb-disabled' : ''}`}
                  onClick={() => changeImage(-1)}
                >
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                    <polyline points="15 18 9 12 15 6"></polyline>
                  </svg>
                </div>
                <div
                  className={`sf-lb-next ${currentIndex === currentImageList.length - 1 ? 'sf-lb-disabled' : ''}`}
                  onClick={() => changeImage(1)}
                >
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                    <polyline points="9 18 15 12 9 6"></polyline>
                  </svg>
                </div>
              </>
            )}
            <div
              ref={imageContainerRef}
              className="sf-lb-img-container"
              onWheel={onWheel}
              onMouseDown={onMouseDown}
              onDoubleClick={onDoubleClick}
              style={{ cursor: scale > 1 ? (isDragging ? 'grabbing' : 'grab') : 'default' }}
            >
              {/* Âä†ËΩΩ‰∏≠Âä®Áîª */}
              {!imageLoadingStates[currentIndex] && (
                <div className="sf-lb-loading">
                  <div className="sf-lb-spinner"></div>
                </div>
              )}
              <img
                src={currentImageList[currentIndex]}
                alt="È¢ÑËßàÂõæÁâá"
                className={`sf-lb-img ${imageLoadingStates[currentIndex] ? 'sf-lb-img-loaded' : 'sf-lb-img-loading'}`}
                style={{
                  transform: `scale(${scale}) translate(${position.x / scale}px, ${position.y / scale}px)`,
                  transition: isDragging ? 'none' : 'transform 0.1s ease-out'
                }}
                onLoad={() => setImageLoadingStates(prev => ({ ...prev, [currentIndex]: true }))}
                onError={() => setImageLoadingStates(prev => ({ ...prev, [currentIndex]: true }))}
                draggable={false}
              />
            </div>
            <div className="sf-lb-counter">
              {currentIndex + 1} / {currentImageList.length}
              {scale > 1 && <span className="sf-lb-zoom-indicator"> ¬∑ {Math.round(scale * 100)}%</span>}
            </div>
          </div>
        )}
      </div>

      {/* ‰∏ªÈ¢òÂàáÊç¢ÊÇ¨ÊµÆÁêÉ */}
      <div className="sf-theme-btn" onClick={toggleTheme} title={isDark ? 'ÂàáÊç¢ÊµÖËâ≤' : 'ÂàáÊç¢Ê∑±Ëâ≤'}>
        {isDark ? (
          <svg fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
          </svg>
        ) : (
          <svg fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
          </svg>
        )}
      </div>

      {/* ÂõûÂà∞È°∂ÈÉ®ÊÇ¨ÊµÆÁêÉ */}
      <div
        className={`sf-back-top ${showBackToTop ? 'sf-visible' : ''}`}
        onClick={scrollToTop}
        title="ÂõûÂà∞È°∂ÈÉ®"
      >
        <svg fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
        </svg>
      </div>
    </div>
  );
}
